import xianMap from './610100.json'
// import xianSubway from './xianSubway.json'
// import BMapSub from 'BMapSub'
export default {
  name: 'Map',
  data () {
    return {
      setPannelShow: false,
      mapChart: null,
      option: {},
      regionData: {
        xincheng: 10,
        beilin: 20,
        lianhu: 30,
        baqiao: 40,
        weiyang: 50,
        yanta: 60,
        yanliang: 70,
        lintong: 80,
        changan: 90,
        gaoling: 100,
        huyi: 10,
        zhouzhi: 60,
        lantian: 70
      },
      min: 0,
      max: 350
    }
  },
  methods: {
    onSubmit () {
      this.setMapChart()
      this.setPannelShow = false
    },
    showPannel () {
      this.setPannelShow = true
    },
    initChart () {
      this.mapChart = this.$echarts.init(document.getElementById('mapChart'))
      this.$echarts.registerMap(name, xianMap)
      // var subwayCityName = '西安'
      // var list = BMapSub.SubwayCitiesList
      // var subwaycity = null
      // for (var i = 0; i < list.length; i++) {
      //   if (list[i].name === subwayCityName) {
      //     subwaycity = list[i]
      //     break
      //   }
      // }
      // const subway = new BMapSub.Subway('mapChart', subwaycity.citycode)
      // subway.setZoom(0.5)
      // console.log(xianSubway.subways.l)
    },
    disposeChart () {
      if (!this.mapChart) {
        return
      }
      this.mapChart.dispose()
      this.mapChart = null
    },
    resizeChart () {
      window.onresize = () => {
        this.mapChart.resize()
      }
    },
    setMapChart () {
      this.option = {
        visualMap: {
          min: 0,
          max: 100,
          text: ['高', '低'],
          realtime: true,
          calculable: false,
          inRange: {
            color: ['lightgreen', 'yellow', 'orangered']
          }
        },
        series: [
          {
            type: 'map',
            mapType: name,
            roam: true,
            label: {
              show: true
            },
            emphasis: {
              label: {
                show: false
              },
              itemStyle: {
                areaColor: 'lightskyblue',
                shadowColor: 'rgba(0, 0, 0, 0.5)',
                shadowBlur: 10
              }
            },
            data: [
              { name: '新城区', value: this.regionData.xincheng },
              { name: '碑林区', value: this.regionData.beilin },
              { name: '莲湖区', value: this.regionData.lianhu },
              { name: '灞桥区', value: this.regionData.baqiao },
              { name: '未央区', value: this.regionData.weiyang },
              { name: '雁塔区', value: this.regionData.yanta },
              { name: '阎良区', value: this.regionData.yanliang },
