              { name: '蓝田县', value: this.regionData.lantian }
            ]
          }
        ]
      }
      this.mapChart.clear()
      this.mapChart.setOption(this.option)
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.initChart()
      this.resizeChart()
      this.setMapChart()
    })
  },
  beforeDestroy () {
    this.disposeChart()
  }
}
